"use client";
import ChatWindow from "@/app/components/ChatWindow";
import MemeGenerator from "@/app/components/MemeGenerator";
import Sidebar from "@/app/components/Sidebar";
import { ChatMessage } from "@/app/types";
import { useState } from "react";

export default function Home() {
  // The messages array mocks the backend data
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [currentQuestion, setCurrentQuestion] = useState("");
  const [currentImage, setCurrentImage] = useState<string | null>(null);
  const [currentAnswer, setCurrentAnswer] = useState("");
  const [loading, setLoading] = useState(false);

  // Simulate sending a message and receiving an answer
  const handleSend = () => {
    if (!currentQuestion.trim() && !currentImage) return;

    // Create a new message object
    const newMessage: ChatMessage = {
      id: Date.now(),
      question: currentQuestion,
      answer: "",
      image: currentImage || undefined,
    };

    // Add to messages (prepend for sidebar order)
    setMessages([newMessage, ...messages]);

    // Clear input fields
    setCurrentQuestion("");
    setCurrentImage(null);
    setCurrentAnswer("");

    // Simulate API delay and answer generation
    setLoading(true);
    const simulatedAnswer = `This is a simulated answer to: "${newMessage.question}"`;
    setTimeout(() => {
      newMessage.answer = simulatedAnswer;
      // Update the messages array with the answer
      setMessages((prev) =>
        prev.map((msg) => (msg.id === newMessage.id ? newMessage : msg))
      );
      setLoading(false);
      // Set the answer for typewriter animation
      setCurrentAnswer(simulatedAnswer);
    }, 1000);
  };
  return <MemeGenerator />;

  return (
    <div className="flex h-screen">
      {/* Sidebar with previous questions */}
      <Sidebar messages={messages} />

      {/* Main Chat area */}
      <div className="flex-1 flex flex-col p-4">
        {/* Chat window with typewriter effect */}
        <ChatWindow answer={currentAnswer} loading={loading} />

        {/* Input area */}
        <div className="mt-4">
          <textarea
            value={currentQuestion}
            onChange={(e) => setCurrentQuestion(e.target.value)}
            placeholder="Type your question here..."
            className="w-full p-2 border border-gray-300 rounded focus:outline-none"
          />
          <input
            type="file"
            accept="image/*"
            onChange={(e) => {
              if (e.target.files && e.target.files[0]) {
                // For mocking purposes, use a URL created from the file object.
                setCurrentImage(URL.createObjectURL(e.target.files[0]));
              }
            }}
            className="mt-2"
          />
          <button
            onClick={handleSend}
            className="mt-2 bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600"
          >
            Send
          </button>
        </div>
      </div>
    </div>
  );
}
